body {
  min-width: 320px;
  color: #fff;
  margin: 0 auto;
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

.container {
  min-width: 320px;
  margin: 0 auto;
  padding: 0 20px;
}

@media (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding: 0 32px;
  }
}

.movies-gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px 175px;
  margin-bottom: 40px;
  display: flex;
}

@media (min-width: 768px) {
  .movies-gallery {
    flex-wrap: wrap;
    gap: 32px;
    margin-bottom: 60px;
  }
}

@media (min-width: 1280px) {
  .movies-gallery {
    gap: 32px 16px;
  }
}

.movie-card {
  color: #000;
  width: 280px;
  transition-property: scale();
  border-radius: 5px;
  transition-duration: .3s;
  transition-timing-function: ease-out;
  overflow: hidden;
}

@media (min-width: 768px) {
  .movie-card {
    width: 336px;
  }
}

@media (min-width: 1280px) {
  .movie-card {
    width: 394px;
  }
}

@media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  .movie-card {
    url: "https://image.tmdb.org/t/p/original${movie.poster_path}";
  }
}

.movie-card__placeholder {
  width: 280px;
  height: 398px;
  background-image: url("coming_soon.ad84676e.jpg");
  background-position: center;
  background-size: cover;
  border-radius: 5px;
}

@media (min-width: 768px) {
  .movie-card__placeholder {
    width: 336px;
    height: 455px;
  }
}

@media (min-width: 1280px) {
  .movie-card__placeholder {
    width: 395px;
    height: 574px;
  }
}

.movie-card__image {
  width: 280px;
  height: 398px;
  border-radius: 5px;
}

@media (min-width: 768px) {
  .movie-card__image {
    width: 336px;
    height: 455px;
  }
}

@media (min-width: 1280px) {
  .movie-card__image {
    width: 395px;
    height: 574px;
  }
}

.movie-card:hover, .movie-card:focus {
  transform: scale(1.05);
}

.movie-card__tittle {
  text-transform: uppercase;
  margin-top: 10px;
  margin-bottom: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 14px;
}

@media (min-width: 768px) {
  .movie-card__tittle {
    margin-top: 8px;
    line-height: 16px;
  }
}

@media (min-width: 1280px) {
  .movie-card__tittle {
    margin-top: 12px;
    font-size: 20px;
    line-height: 24px;
  }
}

.movie-card__info {
  color: #ff6b08;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 14px;
}

@media (min-width: 768px) {
  .movie-card__info {
    line-height: 16px;
  }
}

@media (min-width: 1280px) {
  .movie-card {
    font-size: 20px;
    line-height: 24px;
  }
}

.movie-card__rating {
  width: 36px;
  height: 16px;
  color: #fff;
  background-color: #ff6b08;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  line-height: 14px;
  display: inline-flex;
}

@media (min-width: 1280px) {
  .movie-card__rating {
    width: 40px;
    height: 20px;
  }
}

.list {
  list-style: none;
}

.link, .link:active, .link:visited {
  color: #fff;
  text-decoration: none;
}

.film-icon {
  width: 24px;
  height: 24px;
}

.heart-icon {
  width: 14px;
  height: 12.75px;
  fill: #ff6b08;
  margin-inline: 10px;
}

@media (min-width: 768px) {
  .heart-icon {
    height: 12.88px;
  }
}

.search-icon {
  width: 14px;
  height: 14px;
  position: absolute;
  top: -6px;
  left: 0;
}

.icon-arrow {
  width: 20px;
  height: 20px;
  stroke: currentColor;
  fill: currentColor;
  display: inline-block;
}

.close-icon {
  width: 30px;
  height: 30px;
  stroke: currentColor;
  fill: currentColor;
}

.loader__icon {
  width: 100px;
  height: 125px;
}

.loader__wrapper {
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 20;
  flex-direction: column;
  justify-content: end;
  align-items: center;
  padding-bottom: 10%;
  transition-property: opacity;
  transition-duration: .3s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.loader__wrapper--visable {
  opacity: 1;
}

.loader__message {
  text-align: center;
  -webkit-text-fill-color: transparent;
  background: linear-gradient(0deg, #3c013b 17%, #eb1e1e 100%);
  -webkit-background-clip: text;
  background-clip: text;
  margin: 0;
  padding-bottom: .3rem;
  font-size: 1.5rem;
  font-weight: 700;
}

.rotate-center {
  animation: 1.5s ease-in-out infinite reverse forwards rotate-center;
}

@keyframes rotate-center {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.icon-arrow {
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  font-size: 12px;
  font-weight: 500;
}

.icon-arrow-prev {
  content: url("icon-arrow-prev.814d2afa.svg");
}

.icon-arrow-prev.icon-arrow-hits {
  display: none !important;
}

.icon-arrow-next {
  content: url("icon-arrow-next.91990ac8.svg");
}

.icon-arrow-next.icon-arrow-hits {
  display: none !important;
}

.icon-arrow-first {
  color: #000;
}

.icon-arrow-first.icon-arrow-hits {
  display: none !important;
}

.icon-arrow-last {
  color: #000;
}

.icon-arrow-last.icon-arrow-1 {
  display: none !important;
}

.tui-pagination {
  height: 40px;
  justify-content: center;
  gap: 3px;
  padding-bottom: 60px;
  display: flex;
}

.tui-pagination .tui-page-btn {
  cursor: pointer;
  background-color: #fff;
  border-radius: 5px;
  transition: all .25s;
  width: 40px !important;
  height: 40px !important;
  border: 1px solid #0000 !important;
  margin: 0 !important;
  padding: 7px !important;
  font-weight: 500 !important;
}

.tui-pagination .tui-first {
  position: relative;
  padding: 6px !important;
}

@media (min-width: 768px) {
  .tui-pagination .tui-first {
    left: 44px;
  }
}

.tui-pagination .tui-last {
  position: relative;
  padding: 6px !important;
}

@media (min-width: 768px) {
  .tui-pagination .tui-last {
    left: -44px;
  }
}

.tui-pagination .tui-prev {
  background-color: #f7f7f7;
  position: relative;
  left: 0;
  padding: 10px !important;
}

@media (min-width: 768px) {
  .tui-pagination .tui-prev {
    left: -40px;
  }
}

.tui-pagination .tui-next {
  background-color: #f7f7f7;
  position: relative;
  left: 0;
  padding: 10px !important;
}

@media (min-width: 768px) {
  .tui-pagination .tui-next {
    left: 40px;
  }
}

.tui-pagination .tui-prev-is-ellip, .tui-pagination .tui-next-is-ellip {
  font-size: 12px !important;
}

.tui-pagination .tui-is-selected {
  color: #fff !important;
  cursor: pointer !important;
  background-color: #ff6b08 !important;
}

.tui-pagination .tui-is-selected:hover {
  box-shadow: 0 2px 1px #0000001a, 0 1px 2px #00000012, 0 2px 2px #000;
}

.tui-pagination .tui-is-disabled, .tui-pagination .tui-btn-disabled {
  display: none !important;
}

.tui-pagination .tui-page-nr {
  width: 100%;
  margin: 0;
  display: block;
}

.tui-pagination .tui-non-selected:hover {
  box-shadow: 0 2px 1px #0000001a, 0 1px 2px #00000012, 0 2px 2px #ff6b08;
  background-color: #fff !important;
  border: 1px solid #ff6b08 !important;
}

.notification-container {
  justify-content: center;
  display: flex;
  position: relative;
}

.notification-alert {
  max-width: -moz-fit-content;
  max-width: fit-content;
  text-align: center;
  color: #ff001b;
  margin: 0 auto;
  padding: .5rem 1rem;
  font-size: 13px;
  font-weight: 400;
  line-height: 1.17;
  position: absolute;
  bottom: -70px;
  left: 50%;
  transform: translateX(-50%);
}

@media (min-width: 768px) {
  .notification-alert {
    font-size: 14px;
  }
}

