.modal-devs {
  color: #000;
  background-color: #00000040;
  transition: all .3s ease-in-out;
  display: flex;
  position: fixed;
  inset: 0;
}

.modal-devs-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  box-sizing: border-box;
}

.modal-devs-container {
  width: 90%;
  height: 85%;
  min-width: 300px;
  max-width: 600px;
  max-height: 640px;
  opacity: 1;
  background-color: #fff;
  flex-direction: column;
  margin: auto;
  display: flex;
  overflow-y: auto;
  box-shadow: 0 1px 3px #0000001f, 0 1px 1px #00000024, 0 2px 1px #0003;
}

.modal-devs__header {
  text-align: center;
  margin: 2rem;
  font-size: 28px;
}

.modal-devs__slider {
  width: 100%;
  height: 100%;
  position: relative;
}

.modal-devs__btn {
  width: 50px;
  z-index: 3;
  color: #ff6b08;
  background-color: #0000;
  border: 1px solid #0000;
  border-radius: 5px;
  font-size: 2rem;
  position: absolute;
  top: 80%;
}

.modal-devs__btn:hover, .modal-devs__btn:focus {
  outline: #0000;
  transform: scale(1.25);
}

.modal-devs__slide-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  position: absolute;
  inset: 0;
}

.modal-devs__slide {
  opacity: 0;
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.modal-devs__slide[data-active] {
  opacity: 1;
}

.modal-devs__image {
  width: 45%;
  min-width: 290px;
  height: 70%;
  object-fit: cover;
  object-position: center;
  z-index: 1;
  border-radius: 5px;
  margin: 0 auto;
  display: block;
  overflow: hidden;
  box-shadow: 0 1px 3px #0000001f, 0 1px 1px #00000024, 0 2px 1px #0003;
}

.modal-devs__description {
  width: 100%;
  text-align: center;
  margin-bottom: 2.5rem;
}

.modal-devs__position {
  margin: 0;
  font-size: 20px;
}

.btn-prev {
  left: 1.2rem;
}

.btn-next {
  right: 1.2rem;
}

@media screen and (min-width: 540px) {
  .btn-prev {
    left: 2.5rem;
  }

  .btn-next {
    right: 2.5rem;
  }
}

@media screen and (min-width: 768px) {
  .modal-devs-container {
    width: 60%;
    height: 80%;
    min-width: 300px;
    max-width: 600px;
  }

  .modal-devs__header {
    margin: 2rem;
  }

  .modal-devs__position {
    font-size: 24px;
  }

  .modal-devs__btn {
    font-size: 2.5rem;
    top: 80%;
  }

  .modal-devs .btn-prev {
    left: 3rem;
  }

  .modal-devs .btn-next {
    right: 3rem;
  }
}

@media screen and (min-width: 1280px) {
  .modal-devs__position {
    font-size: 24px;
  }

  .modal-devs__name {
    font-size: 18px;
  }

  .modal-devs__header {
    margin: 2.2rem;
    font-size: 32px;
  }

  .modal-devs__description {
    margin-top: 2rem;
  }

  .modal-devs__btn {
    font-size: 3rem;
